import React, { useState} from "react"
import Button from "../components/Button"

export default function Message() {
    const [message, setMessage] = useState({});
  const apiLink = "http://127.0.0.1:5000/"

  

  const fetchData = () => {
    fetch(apiLink)
    .then(res => res.json())
    .then((data) => {setMessage(data)})
    .catch(console.log)
  }

  const reloadMessage = () => {
    fetchData()
  }

  if (!message.text) fetchData();
    return (
        <div className="message">
            <h2>{message.text}</h2>
            <p className="info"> <b>{message.date}</b>
                <br></br>
                {message.time}
            </p>
            <Button name='Algo Nuevo' action={reloadMessage}/>
            
        </div>
      )
  
  }